



















import { Vue, Prop, Component } from 'vue-property-decorator';
import Icon from "@/components/icon/index.vue";

let beforeScrollTop = window.pageYOffset;

@Component({
  components: {
    Icon
  },
})
export default class Local extends Vue {
  mounted() {
    window.onscroll = this.handlerScroll;
  }
  handlerScroll() {
    let diffX = window.pageYOffset - beforeScrollTop;
    if (diffX > 0) {
    //@ts-ignore
      this.$refs.headerNav.style.height = '35px';
    } else {
       //@ts-ignore
      this.$refs.headerNav.style.height = '60px';
    }
    beforeScrollTop = window.pageYOffset;
  }
}
